<template>
  <section class="modal">
    <div class="container">
      <!-- 클릭시 부모컴포넌트에 클로즈-모달 호출 -->
      <span class="close" @click="this.$emit('close-modal')">&times;</span>
      <div class="imgGroup">
        <img :src="item.path" alt="">
      </div>
      <div class="txt">
        <h1>{{item.name}}</h1>
        <p>
          <span>가격 : </span>
          <span>{{item.price}}</span>
        </p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  // props:["item"], //부모 컴포넌트에서 전달받은 item값
  props:{
    item:{
      type:Object,
      default:function(){
        return{
          name:'',
          price:'',
          path:''
        }
      },
    }
  }
}
</script>

<style scoped>
  .modal{
    position: fixed;
    top: 0; left: 0; bottom: 0; right: 0;
    background-color: rgba(0,0,0,0.9);
    z-index: 999;
  }
  .container{
    width: 600px;
    margin: 100px auto 0;
    background-color: #fff;
    display: flex;
    position: relative;
    animation: scale 0.3s ease;
  }
  @keyframes scale{
    0%{
      opacity: 0;
      transform: scale(0);
    }
    100%{
      opacity: 1;
      transform: scale(1);
    }
  }

  .imgGroup > img{
    display: block;
  }

  .txt{
    padding: 20px;
  }
  .close{
    position: absolute;
    top: 10px; right: 10px;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
  }
  .close:hover{
    color: red;
  }

</style>