<template>
  <header>
    <div class="container">
      <div class="logo">
        <router-link to="/">
          <img src="../assets/images/logo.png" alt="logo">
        </router-link>
      </div> <!--logo-->
      <!-- gnb에 클릭 이벤트 생성, isActive 메서드 호출 -->
      <ul class="gnb" >
        <li @click="isActive" class="on">
          <router-link to="/">HOME</router-link>
        </li>
        <li @click="isActive">
          <router-link to="/about">ABOUT</router-link>
        </li>
        <li @click="isActive">
          <router-link to="/contact">CONTACT</router-link>
        </li>
        <li @click="isActive">
          <router-link to="/kakao">LOGIN</router-link>
        </li>
      </ul>
    </div>
  </header>
</template>

<script>
export default {
  methods:{
    isActive(event){
      // 클릭한 요소의 부모요소인 li 선택
      const targetEl = event.target.parentNode;
      console.log(targetEl);
      // .gnb > li 요소들 모두 선택
      const gnbMenus = document.querySelectorAll('.gnb > li');
      for(let i = 0; i<gnbMenus.length;i++){
        //  .gnb>li의 순번이 클릭한 요소의 부모요소와 같으면 on클래스 추가 그게 아니면 제거
        if(gnbMenus[i] === targetEl){
          gnbMenus[i].className = 'on';
        }else{
          gnbMenus[i].className='';
        }
      }
    },
  },
}
</script>

<style scoped>
  header{
    min-width: 1300px;
    background-color: #fff;
    box-shadow: 0 3px 4px rgba(0,0,0,0.2);
    position: relative;
    z-index: 99;
  }
  header > .container{
    padding: 0 100px;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .gnb{
    display: flex;
  }
  .gnb>li{
    margin-left: 32px;
    font-size: 14px;
    color: #333;
  }
  .gnb > li > a{
    color: #333;
  }
  .gnb > li.on > a{
    color: #10c9c3;
  }
</style>